<template>
	<section  :class="scaleClass">
		<template v-if="showRouterView">
			<router-view ></router-view>
			<!--<child :spid="parentMsg" style="visibility: hidden;;"></child>-->
		</template>
		<template v-else>
			<div class="toolbar"> 
				<el-form :model="filters" :inline="true" >
					<el-form-item>
						<el-input placeholder="品名" v-model="filters.name" size="medium"></el-input>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" @click.native.prevent="getUsers" size="medium">查询</el-button>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" size="medium" @click.native.prevent="MerchandiseTableAdd" >新增</el-button>
					</el-form-item>
				</el-form>
			</div>				
			<el-table 
				ref="multipleTable"
				:data="tableData" 
				stripe   
				border			
				style="width:100%;" 
				:default-sort = "{prop: 'name', order: 'descending'}"
				class="el-table"
		 		v-loading="loading"
		 		@clearSelection="clearSelect"
		 		@selection-change="selectionchange"
				>
			  <el-table-column
			      type="selection"
			      width="55">
			  </el-table-column>
		      <el-table-column
		        prop="spmc"
		        label="品名"
		        min-width="200"
		        sortable
		        >
		      </el-table-column>		     
		      <el-table-column
		        prop="dw"
		        label="单位"
		        sortable
		        align="center"
		        min-width="100"
		        >
		      </el-table-column>
		      <el-table-column
		        prop="price"
		        label="单价"
		        sortable
		        align="center"
		        min-width="100"
		        >
		      </el-table-column>
		      <el-table-column
		        prop="spcode"
		        label="款号"
		        sortable
		        min-width="150"
		        >
		      </el-table-column>
		      <el-table-column
		        prop="createtime"
		        label="创建时间"
		        min-width="200"
		        sortable
		        :formatter="formatDate" 
		        >
		      </el-table-column>
  		      <el-table-column
		        prop="auditstatus"
		        label="状态"
		        :formatter="formatauditstatus"
		        sortable
		        :filters="[{text: '默认', value: 0}, {text: '待审核', value: 1}, {text: '审核通过', value: 5}, {text: '审核不通过', value: 9}]"
      			:filter-method="filterauditstatus"
      			align="center"
		        min-width="100"
		        >
		      </el-table-column>
		      <el-table-column
		        prop="hasimage"
		        label="有图片"
		        :formatter="formathasimage"
		        sortable
		        :filters="[{text: '有', value: true}, {text: '无', value: false}]"
      			:filter-method="filterhasimage"
      			align="center"
		        min-width="100"
		        >
		      </el-table-column>
		      <el-table-column
			      fixed="right"
			      label="操作"
			      min-width="220">
			      <template slot-scope="scope">
			      	<el-row>
			      		<el-button type="primary" size="small" icon="el-icon-upload"  @click.native.prevent="uploadImage(scope.row.id)">上传图片</el-button>
				        <el-button type="success" size="small" icon="el-icon-edit" @click.native.prevent="pwdProductInfo(scope.row)">修改</el-button>
				        <el-button type="danger"  size="small"  @click="deleted" icon="el-icon-delete">删除</el-button>
			      	</el-row>
			      </template>
			  </el-table-column>
			</el-table>
			<div class="pager">
				<template v-if="disabled">
					<el-button disabled>批量删除</el-button>
				</template>
				<template v-else>
					<el-button type="danger">批量删除</el-button>
				</template>
				<el-pagination background layout="prev, pager, next" :total="total" style="float:right;"  
    				@current-change="handleCurrentChange">
				</el-pagination>
			</div>
			<el-dialog
			  title="上傳圖片"
			  :visible.sync="dialogVisible"
			  width="30%">
			  	<el-upload
				  action=""
				  list-type="picture-card"
				  multiple
          		  accept="image/png, image/jpeg"
				  :before-upload="beforeUploadPicture"
				  
				  :on-progress="uploadProgress"
		          :on-remove="handleRemove"
		          :on-success="uploadSuccess"
		          :on-error="uploadError"
				  :show-file-list="true">
				  <i class="el-icon-plus"></i>
				</el-upload>
				<!--<el-dialog :visible.sync="imgVisible">
				  <img width="100%" :src="dialogImageUrl" alt="">
				</el-dialog>-->
				<span slot="footer" class="dialog-footer">
				    <el-button type="primary" @click.native.prevent="addEnsure">上传</el-button>
				    <el-button @click="dialogVisible = false">关闭</el-button>
				</span>
			</el-dialog>
		</template>
		
	</section>
</template>

<script>
  import MerchandiseTable from '../../assets/js/Nav1/MerchandiseTable.js'
  export default MerchandiseTable
</script>
<style scoped lang="scss" >
@import '../../assets/css/Nav1/MerchandiseTable.scss'
</style>

<style>
.el-table th{
    background-color: #eef1f6;
}
.el-table td, .el-table th{
	padding: 6px 0;
}
.el-pagination .btn-next, .el-pagination .btn-prev{
	margin: 0!important;
    border: 1px solid #d1dbe5;
    background-color:#fff!important;
    border-radius: 0!important; 
}
.el-pagination.is-background .el-pager li:not(.disabled).active{
	border: 1px solid #409EFF;
}
.el-pagination.is-background .el-pager li {
    margin: 0 1px!important;
    border: 1px solid #d1dbe5;
    box-sizing: border-box;
    background-color:#fff;
    border-radius: 0;
}
</style>


