<template>
	<section>		
		<template v-if="showRouterView">
				<router-view ></router-view>
		</template>
		<template v-else>
			<div>
				<el-row :gutter="20">
				  <el-col :span="6"><div class="grid-content bg-info"><p>{{idnum}}</p><h3>卡号（CardID）</h3><span @click="ZhizhibaoPwd">修改<i</span><i class="fa fa-drivers-license-o"></i></div></el-col>
				  <el-col :span="6"><div class="grid-content bg-success"><p>{{balance}}RMB</p><h3>余额（Balance）</h3><i class="fa fa-money"></i></div></el-col>
				  <el-col :span="6"><div class="grid-content bg-warning"><p>{{createtime}}</p><h3>开通时间（Time）</h3><i class="fa fa-calendar-times-o"></i></div></el-col>
				  <el-col :span="6"><div class="grid-content bg-danger"><p>{{status}}</p><h3>卡状态（Status）</h3><i class="fa fa-drivers-license-o"></i></div></el-col>
				</el-row>
				<el-row :gutter="20">
				  <el-col :span="16"><div class="grid-content "></div></el-col>
				  <el-col :span="8"><div class="grid-content "></div></el-col>
				</el-row>
				<el-row :gutter="20">
				  <el-col :span="16">
				  	<div class="grid-content bg-purple">
					  	<el-table
					      :data="tableData"
					      style="width: 100%">
					      <el-table-column
					        prop="date"
					        label="Date"
					        width="180">
					      </el-table-column>
					      <el-table-column
					        prop="name"
					        label="Name"
					        width="180">
					      </el-table-column>
					      <el-table-column
					        prop="address"
					        label="Address">
					      </el-table-column>
					    </el-table>
				  	</div>
				  </el-col>
				  <el-col :span="8"><div class="grid-content bg-purple"></div></el-col>
				</el-row>
			</div>
		</template>
	</section>
</template>

<script>
  import MerchandiseTable from '../../assets/js/Nav1/Zhizhibao.js'
  export default MerchandiseTable
</script>

<style scoped lang="scss" >
@import '../../assets/css/Nav1/Zhizhibao.scss'
</style>
<style>
.el-table th{
    background-color: #eef1f6;
}
</style>


