 export default {
      data() {
        return {
    	  showRouterView:false,
    	  idnum:'',
    	  balance:'',
    	  createtime:'',
    	  status:'',
          tableData: [{
            date: '2016-05-03',
            name: 'Tom',
            address: 'No. 189, Grove St, Los Angeles'
          }, {
            date: '2016-05-02',
            name: 'Tom',
            address: 'No. 189, Grove St, Los Angeles'
          }, {
            date: '2016-05-04',
            name: 'Tom',
            address: 'No. 189, Grove St, Los Angeles'
          }, {
            date: '2016-05-01',
            name: 'Tom',
            address: 'No. 189, Grove St, Los Angeles'
          }]
        }
      },
      created(){
		 	this.$api.get('gyswallet/getGysWalletPageList',  {},localStorage.token, success => {
	         	this.idnum=success.obj.idnum;
	         	this.balance=success.obj.balance;
	         	this.createtime=this.moment(success.obj.createtime).format("YYYY-MM-DD HH:mm:ss");
	         	this.status=success.obj.status;
			  },
			 failure=>{
	         	this.$confirm('', failure.msg, {
		          confirmButtonText: '去开通',
		          cancelButtonText: '返回',
		          type: 'warning',
		          center: true
		        }).then(() => {
		          this.$router.push({ path: '/Nav1/Zhizhibao/ZhizhibaoAdd' });
		        }).catch(() => {
		          this.$message({
		            type: 'info',
		            message: 'Delete canceled'
		          });
		        });
			 })
		 	this.getBread();//防止刷新之后显示二级路由的内容
      },
      watch: {
            $route() {
                this.getBread();//监听路由变化，隐藏二级路由的内容
            }
      },
 	  methods:{
  	    getBread(){
			let matched=this.$route.matched.filter(item=>item.name)//$route.matched 将会是一个包含从上到下的所有对象 (副本)。
			if(matched.length>=3){
				this.showRouterView=true;
			}else{
				this.showRouterView=false;
			}
		},
		ZhizhibaoPwd(){
			 this.$router.push({ path: '/Nav1/Zhizhibao/ZhizhibaoPwd' });
		}
 	  }
    }


