import Vue from 'vue'
import Router from 'vue-router'
import MerchandiseTable from '@/components/Nav1/MerchandiseTable'
import Zhizhibao from '@/components/Nav1/Zhizhibao'



Vue.use(Router)

export default new Router({
  //mode:"history",
  routes: [
    {
      path: '/',
      name: 'Login',
      redirect: '/Login',
      component: resolve=>require(['@/components/Login'],resolve)
    },
    {
      path: '/Login',
      name: 'Login',
      component: resolve=>require(['@/components/Login'],resolve)
    },
    {
      path: '/Main',
      name: 'Main',
      component: resolve=>require(['@/components/Main'],resolve),
      redirect:'/Nav1/MerchandiseTable',
      meta: { title: '主菜单', icon: 'dashboard', noCache: true },
      children:[
      {
      	path: '/Nav1/MerchandiseTable',
      	name: 'MerchandiseTable',
      	component:MerchandiseTable, 
      	meta: { title: '商品信息表', icon: 'dashboard', noCache: true },
      	children:[
      	{
      		
	      	path: '/Nav1/MerchandiseTable/MerchandiseTableAdd',
	      	name: 'MerchandiseTableAdd',
	      	component: resolve=>require(['@/components/Nav1/children/MerchandiseTableAdd'],resolve),
	      	meta: { title: '新增', icon: 'dashboard', noCache: true }
      	},
      	{
	      	path: '/Nav1/MerchandiseTable/MerchandiseTableUploadImg',
	      	name: 'MerchandiseTableUploadImg',
	      	component: resolve=>require(['@/components/Nav1/children/MerchandiseTableUploadImg'],resolve),
	      	meta: { title: '上传图片', icon: 'dashboard', noCache: true }
      	},
      	{
	      	path: '/Nav1/MerchandiseTable/MerchandiseTablePwd',
	      	name: 'MerchandiseTablePwd',
	      	component: resolve=>require(['@/components/Nav1/children/MerchandiseTablePwd'],resolve),
	      	meta: { title: '修改', icon: 'dashboard', noCache: true }
      	}
      	]
      },
      {
      	path: '/Nav1/Zhizhibao',
      	name: 'Zhizhibao',
      	component:Zhizhibao,
      	meta: { title: '值值宝', icon: 'dashboard', noCache: true },
      	children:[
      	{
      		path: '/Nav1/Zhizhibao/ZhizhibaoAdd',
	      	name: 'ZhizhibaoAdd',
	      	component: resolve=>require(['@/components/Nav1/children/ZhizhibaoAdd'],resolve),
	      	meta: { title: '开通', icon: 'dashboard', noCache: true }
	      },
	      {
      		path: '/Nav1/Zhizhibao/ZhizhibaoPwd',
	      	name: 'ZhizhibaoPwd',
	      	component: resolve=>require(['@/components/Nav1/children/ZhizhibaoPwd'],resolve),
	      	meta: { title: '修改', icon: 'dashboard', noCache: true }
	      }
      	]
      },
      {
	      path: '/404',
	      name: '404',
	      component: resolve=>require(['@/components/404'],resolve),
	      meta: { title: '404', icon: 'dashboard', noCache: true }
	    },
	    {
	      path: '/500',
	      name: '500',
	      component: resolve=>require(['@/components/500'],resolve),
	      meta: { title: '500', icon: 'dashboard', noCache: true }
	    }
      ]
    },
  ]
})









